<template>
<div class="xy-swiper-container">
        <div class="xy-swiper">
                <div class="swiper-container" ref="mycontainer">
                        <div class="swiper-wrapper">
                                <div class="swiper-slide"><img src="../assets/swiper/0.jpg" /></div>
                                <div class="swiper-slide"><img src="../assets/swiper/1.jpg" /></div>
                                <div class="swiper-slide"><img src="../assets/swiper/2.jpg" /></div>
                                <div class="swiper-slide"><img src="../assets/swiper/3.jpg" /></div>
                                <div class="swiper-slide"><img src="../assets/swiper/4.jpg" /></div>
                                <div class="swiper-slide"><img src="../assets/swiper/5.jpg" /></div>
                        </div>
                        <!-- 如果需要分页器 -->
                        <div class="swiper-pagination" ref="swiperPagination"></div>
                        <!-- 如果需要导航按钮 -->
                        <div class="swiper-button-prev swiper-button-white" ref="swiperPrev"></div>
                        <div class="swiper-button-next swiper-button-white" ref="swiperNext"></div>

                        <!-- 如果需要滚动条 -->
                        <!-- <div class="swiper-scrollbar"></div> -->
                </div>
        </div>
</div>
</template>
<script>
import Swiper from "swiper"
import "../assets/style/swiper.min.css"
export default {
        name:"swiper",
        mounted(){
                // console.log(this.$ref.swiperNext)
                let mySwiper = new Swiper(this.$refs.mycontainer,{

                        watchSlidesProgress: true,//开启这个参数来计算每个slide的progress(进度、进程)。
                        //设置slider容器能够同时显示的slides数量(carousel模式)。可以设置为数字（可为小数，小数不可loop），
                        // 或者 'auto'则自动根据slides的宽度来设定数量。loop模式下如果设置为'auto'还需要设置另外一个参数loopedSlides。                   
                        slidesPerView: 'auto',
                        centeredSlides: true,//设定为true时，active slide会居中，而不是默认状态下的居左。
                        loop: true,// 循环模式选项
                        loopedSlides: 6,//几张轮播图
                        autoplay: true,//回调函数，在slide自动切换开始时执行。

                        //  direction: 'horizontal', // 垂直切换选项
                        // 如果需要前进后退按钮
                        navigation: {
                                nextEl: this.$refs.swiperNext,
                                prevEl: this.$refs.swiperPrev,
                        },
                        // 如果需要滚动条
                        // scrollbar: {
                        //         el: '.swiper-scrollbar',
                        // },
                        // 分页器
                        // pagination: {
		        //         el: this.$refs.swiperPagination,
		        //         //clickable :true,
                        // },
                        on:{
                                progress: function(progress) {
			                for (var i = 0; i < this.slides.length; i++) {
                                                var slide = this.slides.eq(i);
                                                var slideProgress = this.slides[i].progress;
                                                var modify = 1;
                                                if (Math.abs(slideProgress) > 1) {
                                                        var modify = (Math.abs(slideProgress) - 1) * 0.3 + 1;
                                                }
                                                var translate = slideProgress * modify * 260 + 'px';
                                                var scale = 1 - Math.abs(slideProgress) / 6;//控制第一张图片和左右2张的图片的高度
                                                var zIndex = 999 - Math.abs(Math.round(10 * slideProgress));
                                                slide.transform('translateX(' + translate + ') scale(' + scale + ')');
                                                slide.css('zIndex', zIndex);
                                                slide.css('opacity', 1);
                                                if (Math.abs(slideProgress) > 3) {
                                                        slide.css('opacity', 0);
                                                }
                                        }
                                },
                                setTransition: function(transition) {
			                for (var i = 0; i < this.slides.length; i++) {
				                var slide = this.slides.eq(i)
				                slide.transition(transition);
                                        }

                                }
                        }
                        
                })
        }
}
</script>
<style lang="stylus" scoped>

.xy-swiper-container
        width : 100%
        height:300px
        margin-bottom:20px
        .xy-swiper
                width:1080px
                height:300px
                margin:20px auto 0
                .swiper-container
                        width:100%
                        height:100% 
                        .swiper-slide
                                width:750px
                                height:300px
                                background: #fff;
                                img
                                        display : block
                                        width:100%
                                        height:100%
                        .swiper-button-prev::after
                                width:100px
                                height:100px
                                background:red
                                z-index:100
                                position:absolute

</style>