            this.showList = !this.showList
            // 当列表展示的时候，让小三角旋转180度，当合上的时候，小三角旋转到0度
            if(this.showList == false){
                this.$refs.changeXSJ.style.transform = "rotate(0deg)"
            }else{
                this.$refs.changeXSJ.style.transform = "rotate(180deg)"
            }
        },
        // 关闭蒙层
        closeMasking(){
            this.isShow = false
            this.showList = false
            document.body.style.overflow='scroll';//点击时滚动条出现
        },
        stopMAOPAO(event){
            // console.log(e)
            event.stopPropagation(); 
        },
        clickList(item,e){
            console.log(item,e)
            this.iphoneCount = item.count
        }
    }

}
</script>

