<script>
import upLoading from "./uploading"
import axios from 'axios'
import Vue from 'vue'
Vue.prototype.$axios = axios
Vue.prototype.HOME = '/api'
// https://www.ximalaya.com/revision/suggest?kw=xin&paidFilter=false&size=6

// "http://localhost:8080/api/revision/suggest?kw=gui&paidFilter=false&size=6"
export default {
        name:"xmNav",
        data(){
                return{
                        ifShow:false,
                        showContent:"",
                        myArr:[],
                        isShow:false
                }
        },
         methods:{
                showPt(){
                        console.log("1")
                        this.ifShow = true
                },
                closePt(){
                        this.ifShow = false
                },
                inputFn(){
                        console.log('1')
                        if(this.showContent.length > 0){
                                this.$axios.get(this.HOME+`/revision/suggest?kw=${this.showContent}&paidFilter=false&size=6`)
                                .then((res)=>{
                                        console.log(res)
                                        this.myArr = (res.data.data.result.queryResultList).slice(0,11)
                                })
                                this.isShow = true
                        }else{
                                this.isShow = false
                        }
                }
        },
        components:{
                upLoading,
        }

}
</script>
